.wrapper .info {
  overflow: hidden;
}
.wrapper .info .img {
  float: left;
  display: block;
  border-radius: 10px;
}
.wrapper .info .introduce {
  float: left;
  margin-left: 15px;
}
.wrapper .info .introduce .menu-name {
  font-weight: 600;
  color: red;
  border-bottom: 1px solid #f1f1f1;
}
.wrapper .info .introduce .tags {
  width: 360px;
  height: 45px;
  border-bottom: 1px solid #f1f1f1;
}
.wrapper .info .introduce .statistic {
  width: 360px;
}
.wrapper .info .introduce .statistic .statistic-item {
  display: inline-block;
  margin-right: 60px;
}
.wrapper .describe {
  margin: 10px 0 0 15px;
  font: 16px/1.6 "Hiragino Sans GB", STHeiti, "\5FAE\8F6F\96C5\9ED1", "Microsoft YaHei", Helvetica, Arial, serif;
}
.wrapper .describe .marks {
  color: #BFBFBF;
  font-size: 40px;
  height: 16px;
  line-height: 100%;
  display: inline-block;
  overflow: hidden;
}
.wrapper .ingredients h3 {
  color: #52c41a;
}
.wrapper .ingredients h3 .icon {
  margin: 0 10px;
}
.wrapper .process h3 {
  color: #52c41a;
}
.wrapper .process h3 .icon {
  margin: 0 10px;
}
.wrapper .process .process-item {
  display: flex;
  margin: 15px 0;
  padding-bottom: 15px;
  border-bottom: 1px solid #f1f1f1;
}
.wrapper .process .process-item .process-item_img {
  border-radius: 5px;
}
.wrapper .process .process-item .process-item_des {
  flex: 10 1;
}
.wrapper .process .process-item .process-item_des .step {
  margin-left: 20px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border: 1px solid #1DA57A;
  border-radius: 50%;
  font-weight: 600;
  color: #1DA57A;
}
